import axios from '@/plugins/axios'
import url from '../url.js'

const API = {}

API.teacher = function(page,state) {
	return axios.get(url.Teacher, {page: page,state: state})
}

// 获取学生列表
API.students = function(page) {
	return axios.get(url.Students, {page: page})
}

// 新增学生
API.student = function(data) {
	return axios.post(url.Student, data)
}

// 搜索学生
API.search = function(number) {
	return axios.get(url.Students, {number:number})
}

// 新增年级
API.grade = function(data) {
	return axios.post(url.Grade, data)
}

// 获取年级
API.grades = function(page) {
	return axios.get(url.Grades, {page: page})
}

// 新增班级
API._class = function(data) {
	return axios.post(url.Class, data)
}

// 获取班级
API.classes = function(page) {
	return axios.get(url.Classes, {page:page})
}

API.classes1 = function(grade_id,limit) {
	return axios.get(url.Classes, {grade_id:grade_id,limit:limit})
}

// 设备管理
API.device = function(data) {
	return axios.post(url.Device, data)
}

// 获取设备
API.devices = function(page) {
	return axios.post(url.Devices, {page: page})
}

// 获取家长
API.family = function(page) {
	return axios.get(url.Family, {page: page})
}

// 公告管理
API.announcement = function(page) {
	return axios.get(url.Announcement,{page: page})
}

export default API